@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap);

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Poppins', sans-serif;
}
body{
    --twitter-color:#50b7f5;
    --twitter-background:#e6ecf0;
}
.app{
    display:flex;
    height: 100vh;
    max-width: 1300px;
    margin-left: auto;
    margin-right: auto;
    padding: 0 10px;
}
.sidebarOption{
display: flex;
align-items: center;
cursor: pointer;
}
.sidebar{
    border-right: 1px solid var(--twitter-background);
    flex: 0.3 1;
    margin-top: 20px;
    padding-left: 20px;
    padding-right: 20px;
}
.t1{
    color: var(--twitter-color);
    font-size: 30px !important;
    margin-left: 20px;
    margin-right: 20px;  
    cursor: pointer;
    margin-bottom: 20px;
}
.sidebarOption:hover{
    background: #e8f5fe;
    border-radius: 30px;
    color: var(--twitter-color);
    transition: color 100ms ease-out;
}
.sidebarOption i{
    padding: 20px;
}
.sidebarOption h2{
    font-weight: 800;
    font-size: 20px;
    margin-right: 20px;
    font-family: 'Poppins', sans-serif;
}
.active{
    color: var(--twitter-color);    
}
.tweet{
    width: 100%;
    height: -webkit-max-content;
    height: max-content;
    padding: 10px;
    border-radius: 30px !important;
    border:none !important;
    background: var(--twitter-color) !important;
    font-size: 1.2em;
    font-family: 'Poppins', sans-serif;
    font-weight: 900 !important;
    color: white !important;
    margin-top: 20px !important;
    cursor: pointer;
}
.feed{
    flex: 0.4 1;
    border: 1px solid var(--twitter-background);
    min-width: -webkit-fit-content;
    min-width: -moz-fit-content;
    min-width: fit-content;
    overflow-y: scroll;
}
.header{
    position: -webkit-sticky;
    position: sticky;
    z-index: 100;
    top: 0;
    background-color: white;
    border: 1px solid var(--twitter-background);
    padding: 15px 20px;
}
::-webkit-scrollbar{
    width: 0;
}
.tweetbox{
    padding-bottom: 10px;
    border-bottom: 8px solid var(--twitter-background);
    padding-right: 10px;
}
.tweetbox > form{
    display: flex;
    flex-direction: column;
}
.inputTweet{
    padding: 20px;
    display: flex;
}
.inputTweet>input{
    flex: 1 1;
    margin-left: 20px;
    padding-left: 5px;
    font-size: 20px;
    border: none;
}
.tweetButton{
    width: 80px;
    height: -webkit-max-content;
    height: max-content;
    padding: 10px;
    border-radius: 30px !important;
    border:none !important;
    background: var(--twitter-color) !important;
    /* font-size: 1.2em; */
    font-family: 'Poppins', sans-serif;
    font-weight: 900 !important;
    color: white !important;
    margin-top: 20px !important;
    margin-left: auto;
    cursor: pointer;
}
.inputImg{
    border: none;
    padding-left: 20px;
    margin: auto auto;
    width:80%;
}
.avatar{
    font-size: 1.5em;
}
.post{
    display: flex;
    align-items: flex-start;
    margin: 10px;
    padding: 5px;
    border-bottom: 2px solid var(--twitter-background);
}
.postdp{
    margin-right: 10px;
}
.postbody{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 100%;
    height: -webkit-max-content;
    height: max-content;
}
.img{
    margin-top: 10px;
    width: -webkit-max-content;
    width: max-content;
    height: -webkit-max-content;
    height: max-content;
    margin: 10px auto;
}
.img>img{
    width: 400px;
    height:auto;
    border-radius: 20px;
}
.postfooter{
    display: flex;
    justify-content: space-between;
    margin-top: 20px;
    margin-bottom: 20x;
}
.postfooter>i{
    cursor: pointer;
}
.headertxt h3 > i{
color: var(--twitter-color);
font-size: 0.8em;
}
.headertxt h3 > span{
    font-size: 12px;
    color: gray;
    padding-left: 6px;
}
@media (max-width:991px){
.img>img{
    width: 300px;
}    
}
.widgets{
    flex: 0.3 1;
    overflow-y: scroll;
}
.wheader{
    position: -webkit-sticky;
    position: sticky;
    z-index: 100;
    top: 0;
    background-color: white;
    border: 1px solid var(--twitter-background);
    padding: 15px 20px;
}
.wheader >h2{
    font-size: 0.9em;
}
.wheader>h2>i{
    color: var(--twitter-color);
}
.wbody{
    padding: 20px;
    border-radius: 20px;
    margin: 20px;
    background:var(--twitter-background);
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: auto;
    height: auto;  
}
.text{
    margin-left: 9px;
}
.text>h2{
    font-size: 1.3em;
    font-weight: 500;
}
.news-image{
    width:auto;
    height: auto;
}
.news-image>img{
    border-radius: 20px;
    height: auto;
    width: 300px;
}
.more{
    border-radius: 5px;
    padding: 5px;
    border: none;
    background: var(--twitter-color);
    color: white;
    width: 100%;
    cursor: pointer;
}
